

.pic {
    width: 152.71px;
    height: 167.41px;

}

.summary {
    font-family: 'InaiMathi';
    font-style: normal;
    font-weight: 400;
    font-size: 13px;
    line-height: 16.92px;
    letter-spacing: 0.15em;
    color: #4A1B00;
}

/* Medium devices such as tablets (768px and up) */
@media only screen and (max-width:48em) {
    .teamMemberWrapper {
        display: flex;
        flex-direction: column;
        width:100%;
        flex-basis: 50%;
        flex-grow: 1;
    }
    .pic {
        min-width: 152.71px;
        min-height: 167.41px;
        flex-grow: 1;
        width: 100%;
        height: auto;
    }
}

/* Medium devices such as tablets (768px and up) */
@media only screen and (min-width:48em) {
    .summary {
        font-weight: 400;
        font-size: 30px;
        line-height: 38px;
        letter-spacing: 0.15em;
        color: #4A1B00;
    }
    .pic {
        width: 100%;
        height: auto;
    }
    .teamMemberWrapper {
        display: flex;
        flex-direction: column;
        max-width: 366.71px;
    }
}
  
